import axios from "axios";
import React, { useEffect, useState } from "react";
import { StrictMode } from "react";
import ReactDOM from "react-dom";
import "./styles.css";

export const App = () => {
  const [datax, setdata] = useState(null);
  const [sayac, setsayac] = useState(1);

  function handle() {
    setsayac((say) => say + 1);
    const fetchdatax = async () =>
      await axios.get("https://jsonplaceholder.typicode.com/users/" + sayac);
    fetchdatax().then((t) => setdata(t.data));
  }
  const fetchdata = async () =>
    await axios.get("https://jsonplaceholder.typicode.com/users/" + sayac);

  useEffect(() => {
    fetchdata().then((t) => setdata(t.data));
  }, []);
  if (!datax) return <div>loading...</div>;

  console.log(datax);
  return (
    <div>
      Butona Başta 2 kez tıklayın
      <br />
      <button onClick={handle}>+</button>
      <br />
      {datax.id}
      <br />
      {datax.name}
    </div>
  );
};
